<template>
<el-container style="height: 100vh;">
  <div class="mask" @click="maskCloseMenu"></div>
  <el-aside class="aside-nav" :class="{'aside-nav-isCollapse':data.isCollapse}">
    <el-scrollbar height="100%">
      <router-link :to="{name: 'Home'}" class="flex-rCenter" style="padding: 8px;">
        <img width="50" height="50" src="../assets/images/logo.png" alt="">
        <div style="margin-left: 5px; color: white;">
          <b style="font-size: 15px; width: 100%; white-space: nowrap;word-break: keep-all; text-overflow: ellipsis;">vue3-eplus-admin</b>
        </div>
      </router-link>
      <el-menu
      :uniqueOpened="true"
      :default-active="data.currentRoute.path"
      class="el-menu-vertical-demo"
      @select="handleSelect"
      @open="handleOpen"
      @close="handleClose"
      background-color="#001529"
      text-color="#bfcbd9"
      active-text-color="#fff"
      :router="true"
      :collapse="false"
    >
      <template :key="route.name" v-for="route in this.router.getRoutes()">
        <template v-if="route.meta.asideNav">
          <template v-if="!route.meta.asideNav.submenu && route.meta.asideNav.master">
            <el-menu-item :index="route.path" :disabled="route.meta.asideNav.disable">
              <i v-if="route.meta.asideNav.iconClass" :class="route.meta.asideNav.iconClass"></i>
              <template #title>{{route.meta.title}}</template>
            </el-menu-item>
          </template>
          <template v-else-if="route.meta.asideNav.master">
            <el-submenu :index="route.path">
              <template #title>
                <i v-if="route.meta.asideNav.iconClass" :class="route.meta.asideNav.iconClass"></i>
                <span>{{route.meta.title}}</span>
              </template>
              <el-menu-item-group>
                <template :key="submenu" v-for="submenu in route.meta.asideNav.submenu">
                  <template :key="route.name" v-for="route in this.router.getRoutes()">
                    <template v-if="route.name==submenu">
                      <template v-if="route.meta.asideNav">
                        <template v-if="!route.meta.asideNav.submenu">
                          <el-menu-item :index="route.path">
                            <i v-if="route.meta.asideNav.iconClass" :class="route.meta.asideNav.iconClass"></i>
                            <template #title>{{route.meta.title}}</template>
                          </el-menu-item>
                        </template>
                        <template v-else>
                          <el-submenu :index="route.path">
                            <template #title>
                              <i v-if="route.meta.asideNav.iconClass" :class="route.meta.asideNav.iconClass"></i>
                              <span>{{route.meta.title}}</span>
                            </template>
                            <el-menu-item-group>
                              <template :key="submenu" v-for="submenu in route.meta.asideNav.submenu">
                                <template :key="route.name" v-for="route in this.router.getRoutes()">
                                  <template v-if="route.name==submenu">
                                    <template v-if="route.meta.asideNav">
                                      <template v-if="!route.meta.asideNav.submenu">
                                        <el-menu-item :index="route.path">
                                          <i v-if="route.meta.asideNav.iconClass" :class="route.meta.asideNav.iconClass"></i>
                                          <template #title>{{route.meta.title}}</template>
                                        </el-menu-item>
                                      </template>
                                      <template v-else>
                                        <!-- 套娃：已有2层子菜单 -->
                                      </template>
                                    </template>
                                  </template>
                                </template>
                              </template>
                            </el-menu-item-group>
                          </el-submenu>
                        </template>
                      </template>
                    </template>
                  </template>
                </template>
              </el-menu-item-group>
            </el-submenu>
          </template>
        </template>
      </template>


    </el-menu>
    </el-scrollbar>
  </el-aside>
  <el-container>
    <el-header class="header" style="margin: 0;">
      <nav class="top-nav flex-rCenter animate__animated animate__bounce">
        <div>
          <el-button class="menuSwitch" size="medium" style="margin-left: 1rem;" type="" icon="el-icon-menu" @click="menuSwitch"></el-button>
        </div>
        <div>
          <h3 @click="test" style="color: white; word-break: keep-all; text-overflow: ellipsis;">后台管理系统</h3>
        </div>
        <div class="flex-rCenter">
          <el-tooltip class="item fullscreen-switch" effect="dark" content="全屏" placement="bottom">
            <el-button style="background-color: transparent; transform: scale(1.2); border: none" @click="toggleFullScreen" circle icon="el-icon-full-screen"></el-button>
          </el-tooltip>
          <el-dropdown style="margin-right: 1rem" class="user-box">
            <div class="flex-rCenter">
              <img style="margin-right: 0.5rem;border-radius: 50%; width: 30px; height: 30px;" src="@/assets/images/avatar.jpg" alt="">
              <b class="el-dropdown-link" style="color: #606266">小虎</b>
            </div>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item icon="el-icon-s-custom">个人资料</el-dropdown-item>
                <el-dropdown-item icon="el-icon-sold-out" @click="logout">退出登录</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </nav>
      <div class="breadcrumb flex-rCenter">
        <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 1rem">
          <template v-for="route in router.getRoutes()" :key="route.name">
            <templaet v-if="route.name==router.currentRoute.value.name">
              <templaet v-if="route.meta.breadcrumb">
                <el-breadcrumb-item :to="item.path?{path:item.path}:null" v-for="item in route.meta.breadcrumb" :key="item.title">{{ item.title }}</el-breadcrumb-item>
              </templaet>
              <templaet v-else>
                <el-breadcrumb-item :to="{path:route.path}">{{ route.meta.title }}</el-breadcrumb-item>
              </templaet>
            </templaet>
          </template>
        </el-breadcrumb>
      </div>
    </el-header>
    <el-main>
      <!-- 需要缓存的视图组件 -->
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive" data-aos="fade-down"
        :startIntro="startIntro"
        ></router-view>
      </keep-alive>
      <!-- 不需要缓存的视图组件 -->
      <router-view v-if="!$route.meta.keepAlive" data-aos="fade-down"
        :startIntro="startIntro"
      ></router-view>
    </el-main>
  </el-container>
</el-container>
</template>

<script lang="ts">
import {
  reactive,
  defineComponent,
  watch,
  ref,
  markRaw,
  onBeforeUpdate,
  inject,
  onBeforeMount,
  computed,
  onUpdated
} from 'vue';
import Statistics from '@/components/Statistics.vue';
import {Router, useRouter } from "vue-router";
import {Store, useStore} from "vuex";
import NProgress from "nprogress";
import nProgress from "nprogress";
import {Step} from "intro.js";

export default defineComponent({
  name: 'Layout',
  setup(props, context) {
    const router:Router = useRouter();
    const store:Store<any> = useStore();
    const velocity:any = inject('velocity');
    const introJs:any = inject('introJs');
    const currenMediaWidth = computed(()=> store.state.currenMediaWidth);
    let data = reactive({
      isCollapse: false,
      currentRoute: router.currentRoute.value
    });
    onBeforeMount(()=>{
      if (window.matchMedia(store.state.media_md).matches) {
        data.isCollapse = true;
      }else{
        data.isCollapse = false;
      }
    });
    onBeforeUpdate(()=>{
      data.currentRoute = router.currentRoute.value;
    });
    onUpdated(()=>{
      NProgress.start();
      NProgress.done();
    });
    function toggleFullScreen(){
      if (document.fullscreenElement) {
        document.exitFullscreen();
      } else {
        document.documentElement.requestFullscreen();
      }
    }
    function menuSwitch(){
      if (window.matchMedia(store.state.media_md).matches) {
        let menu:any = document.querySelector('.aside-nav');
        let mask:any = document.querySelector('.mask');
        menu.classList.add('aside-nav-open');
        velocity(mask, {
          opacity: 1
        }, {
          duration: 400,
          display: 'block'
        });
      } else {
        data.isCollapse=!data.isCollapse;
      }
    }
    function maskCloseMenu(e:any){
      let menu:any = document.querySelector('.aside-nav');
      let mask:any = e.target;
      menu.classList.remove('aside-nav-open');
      velocity(menu, {
        xxx: null
      }, {
        duration: 400,
        begin: (els:any)=>{
          els[0].style.setProperty('position', 'absolute');
          els[0].style.setProperty('height', '100%');
          els[0].style.setProperty('z-index', '10');
        },
        complete: (els:any)=>{
          els[0].style.setProperty('position', null);
          els[0].style.setProperty('height', null);
          els[0].style.setProperty('z-index', null);
        }
      });
      velocity(mask, {
        opacity: 0
      }, {
        duration: 400,
        display: 'none'
      });
    }
    watch(currenMediaWidth, (nv, ov)=>{
      if (window.matchMedia(store.state.media_md).matches) {
        data.isCollapse = true;
      }else{
        data.isCollapse = false;
      }
    });
    function logout(){
      window.localStorage.removeItem('userToken');
      router.push({path: '/login'});
    }
    function test(){

      console.log('test')

    }
    function startIntro(){
      introJs().setOptions({
        prevLabel: "上一步",
        nextLabel: "下一步",
        skipLabel: "跳过",
        doneLabel: "完成",
        steps: [
          {
            title: '小虎',
            intro: "欢迎来到引导页~<br/>准备开始了嘛！"
          },
          {
            element: document.querySelector('.aside-nav'),
            title: '菜单栏',
            intro: "以路由的结构渲染的菜单栏"
          },
          {
            element: document.querySelector('.menuSwitch'),
            title: '菜单开关',
            intro: "用于折叠和展开菜单栏"
          },
          {
            element: document.querySelector('.breadcrumb'),
            title: '面包屑',
            intro: "用于记录当前路由结构"
          },
          {
            element: document.querySelector('.fullscreen-switch'),
            title: '全屏开关',
            intro: "用于设置是否全屏"
          },
          {
            element: document.querySelector('.user-box'),
            title: '用户信息',
            intro: "可以访问用户的相关功能"
          }
        ]
      }).start();
    }
    const handleSelect = (key:any, keyPath:any) => {return null;};
    const handleOpen = (key:any, keyPath:any) => {return null;};
    const handleClose = (key:any, keyPath:any) => {return null;};
    return {
      router,
      data,
      toggleFullScreen,
      menuSwitch,
      maskCloseMenu,
      logout,
      test,
      startIntro,
      handleSelect,
      handleOpen,
      handleClose
    };
  },
});


</script>

